import React, { useState, useEffect } from "react";



interface GitHubProps {
  login: string
}

interface GitHubUser {
  name: string
  login: string
  avatar_url: string
  location: string
}


export const GitHubUser = ({ login }: GitHubProps) => {



  // const loadJSON = (key: string): GitHubUser | null => {
  //   let item: string | null = localStorage.getItem(key)
  //   if (!item) {
  //     return null 
  //   };
  //   let itemJSON = JSON.parse(item)
  //   if (key && itemJSON){

  //   }
  // }

  // const saveJSON = (key: string, data: string) =>
  //   localStorage.setItem(key, JSON.stringify(data));



  const [data, setData] = useState();

  useEffect(() => {
    if (!login) return;
    fetch(`https://api.github.com/users/${login}`)
      .then(response => response.json())
      .then(setData)
      .catch(console.error);
  }, [login]);

  if (data) {
    return (
      <div className="async">
        <pre className="async__wrapper" >{JSON.stringify(data, null, 2)}</pre>
      </div>
    )
  }

  return null;

}

//*          useState()
//* ,     null,   null,   React   .   :     . 
//*      useEffect.    -   .  ,        -   JSON.       JSON   setData,      ,        .  useEffect    ,     login.

